.main {
  width: 100%;
  height: 100vh;
}
.title {
  width: 100%;
  height: 30px;
  box-sizing: border-box;
  padding: 0 10px;
  line-height: 30px;
  text-align: left;
  background: #001529;
  color: #ffffff;
  font-weight: bold;
}
.pages {
  width: 100%;
  height: calc(100% - 30px);
}
.pages .menu {
  width: 256px;
  height: 100%;
  display: inline-block;
  float: left;
  box-shadow: 3px 0 2px #e5e5e5;
}
.pages .menu > div {
  width: 100%;
  line-height: 50px;
  text-align: left;
  background: #001529;
}
.pages .breadcrumb {
  width: calc(100% - 256px);
  height: 30px;
  line-height: 30px;
  display: inline-block;
  background: #f9f8f8;
  text-align: left;
  padding: 0 10px;
}
.pages .content {
  width: calc(100% - 256px);
  height: calc(100% - 30px);
  display: inline-block;
  box-sizing: border-box;
  padding: 10px;
}
